<template>
<!-- 列表中间卡片 -->
  <div class="summery-card img  clearfix">
      <div class="card-img left">
          <img :src="summeryObj.summeryImg" alt="">
      </div>

      <div class="card-text left flex-box flex-direction main-space">
          <p class="title">{{ summeryObj.summeryName }}</p>
          <p>{{ summeryObj.summeryTotal }}</p>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    summeryObj: {
      type: Object,
      default: () => {
        return {
          summeryName: "合计单数",
          summeryTotal: 0,
          summeryImg: require("../images/jishikaitai.png")
        };
      }
    }
  },
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
@import "src/styles/flex.scss";

.summery-card {
  @include wh(100%, 100%);
  @include fc(14px, #5a8cff);
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 18%;
}

.card-img {
  @include wh(80px, 80px);
  border-radius: 40px;
  // margin: 15px 0 15px 18%; 

  img {
    @include wh(80px, 80px);
  }
}

@media screen and (max-width: 1450px) {
  .summery-card {
    padding-left: 8%;
  }
}

.card-text {
  // margin-top: 15px;
  // margin-bottom: 15px;
  margin-left: 15px;
  height: 80px;
  max-width: 34%;
  word-wrap: break-word;
}
</style>




